import { CourseQueryResponse } from "__generated__/CourseQuery.graphql";
import ModifyReviewMutation from "actions/ModifyReviewMutation";
import { Button } from "antd";
import Collapse from "antd/lib/collapse";
import Typography from "antd/lib/typography";
import { Fragment, useState } from "react";
import { secured } from "react-abac";
import ReactMarkdown from "react-markdown";
import { useRecoilValue } from "recoil";
import { permissions, Role } from "services/abac";
import environment from "services/gqlenv";
import { SESSION_STATE } from "store";

const Reviews = (props: {
  reviews: CourseQueryResponse["getCourseData"]["reviews"];
}) => {
  const { Panel } = Collapse;
  const { reviews } = props;
  const { role } = useRecoilValue(SESSION_STATE);

  const Moderate = (props: { review: typeof reviews[0] }) => {
    return (
      <Button
        onClick={(e) => {
          ModifyReviewMutation.commit(
            environment,
            props.review.id,
            !props.review.approved
          );
        }}
      >
        {props.review.approved === true ? "Takedown" : "Approve"}
      </Button>
    );
  };

  const SecuredModerate = secured({
    permissions: permissions.MODERATE_REVIEW,
    mapPropsToData: (props: { review: typeof reviews[0] }) => props,
  })(Moderate);

  return (
    <Fragment>
      <Collapse accordion>
        {reviews.map((review, index) => {
          if (review.approved) {
            return (
              <Panel
                header={`${review.semester} Prof : ${review.instructor}`}
                key={review.id}
                extra={<SecuredModerate review={review} />}
              >
                <Typography>
                  <ReactMarkdown children={review.grading} />
                </Typography>
              </Panel>
            );
          } else if (role === Role.MANAGER || role == Role.ADMIN) {
            return (
              <Panel
                header={`${review.semester} Prof : ${review.instructor}`}
                key={review.id}
                extra={<SecuredModerate review={review} />}
              >
                <Typography>
                  <ReactMarkdown children={review.grading} />
                </Typography>
              </Panel>
            );
          }
        })}
      </Collapse>
    </Fragment>
  );
};

export default Reviews;
